.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#create-post-form {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  transition: transform 0.3s ease-in-out;
  background: white;
  padding: 20px;
  transform:translateY(100%);
}

.create-post-form-open {
  animation-duration:1s;
  animation-name:postform-slidein;
  animation-fill-mode: forwards;
}

@keyframes postform-slidein{
    from {bottom:-100%;}
    to {bottom:50%;}
}

@keyframes postform-slideout{
    from {bottom:50%;}
    to {bottom:-100%;}
}

.close-icon {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
  font-weight: bold;
  animation-duration:1s;
  animation-name:postform-slideout;
  animation-fill-mode: forwards;
}

.selected {
  height: auto;
}